import { Component ,OnInit} from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ModalController } from '@ionic/angular';
import {DetailalmacenPage} from './../detailalmacen/detailalmacen.page'
import {ConfigService} from './../config.service'
import { material } from './../shared/material';
@Component({
  selector: 'app-tab4',
  templateUrl: './tab4.page.html',
  styleUrls: ['./tab4.page.scss'],
})
export class Tab4Page implements OnInit{
  public items;
  public date1;
  public date2;
  filter: material = new material();
  constructor(private host:ConfigService,private http: HttpClient,private modal: ModalController) { 
    
  }
  ngOnInit(){
    this.http.get<any>(this.host.getHost() + '/DisponibilidadAlmacen').subscribe(data => {
      this.items = data;
    })
  }
  async itemSelected(item: string) {
    const modal = await this.modal.create({
      component: DetailalmacenPage,
      componentProps: {
        items:item
      }
    });
    modal.onDidDismiss().then(()=>{
      this.http.get<any>(this.host.getHost() + '/DisponibilidadAlmacen').subscribe(data => {
        this.items = data;
      })
    });
    return await modal.present();
  }
  doRefresh(event) {
    this.http.get<any>(this.host.getHost() + '/DisponibilidadAlmacen').subscribe(data => {
      this.items = data;
    })

    setTimeout(() => {
      console.log('Async operation has ended');
      event.target.complete();
    }, 1000);
  }
}
